body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
    font-family: Futura;
    src: url(/personal_website/static/media/FuturaPT-ExtraBold.309054a2.woff2);
    font-display: fallback;
}

@font-face {
    font-family: Adelle;
    src: url(/personal_website/static/media/AdelleSansEXT-Regular.822e6b83.woff2);
    font-display: fallback;
}

body {
    font-family: Adelle;
}
h1,h2,h3,h4 {
    font-family: Futura;
}
#home {
  max-width: 80ch;
  padding: 1rem;
  margin: auto;
}

#tech-stack {
  margin: 70px 0;
}

@media (max-width: 600px) {
  #tech-stack-logos {
    width: 100%;
    margin: 0;
    padding: 0;
  }

}


#tech-stack-logos {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-flow: wrap row;
  width: 80%;
  margin: auto;
}

#tech-stack-logos > .tech {
  position: relative;
}

.tech > img {
  width: 80px;
  margin: 10px;
}

.tech:hover > img {
  opacity: 0.2;
  transition: .3s ease;
}


.tech > p {
  color: #892c7e;
  font-weight: bold;
  opacity: 0;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  bottom: 30%;
}

.tech:hover > p {
  opacity: 1;
  transition: .3s ease;
}

/* Social Media */

#socia-media {
  margin: 70px 0;
}

@media (max-width: 100px) {
  #social-media-logos {
    width: 100%;
    margin: 0;
    padding: 0;
  }

}


#social-media-logos {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-flow: wrap row;
  width: 80%;
  margin: auto;
}

#social-media-logos > .social {
  position: relative;
}

.social > a> img {
  width: 100px;
  margin: 10px;
}

.social:hover >a> img {
  opacity: 0.2;
  transition: .3s ease;
}


.social > p {
  color: #892c7e;
  font-weight: bold;
  opacity: 0;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  bottom: 30%;
}

.social:hover > p {
  opacity: 1;
  transition: .3s ease;
}



.resize {
    text-align: right;
}

.img {
    width: 300px;
    height: auto;
    display: block;
    margin-right: auto;
    margin-left: auto;
    border-radius: 50%;
  }


.navbar {
    background-color: #273539;
    opacity: 8;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .nav-menu {
    background-color: #273539;
    opacity: 8;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .nav-text a:hover {
    background-color: #e9c46a;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #273539;
    opacity: 8;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
